import React, { useState } from 'react';
import { Collapse, Button, CardBody, Card, ListGroupItem, Row, Col } from 'reactstrap';
import { Line} from "react-chartjs-2";
  
  import {
    chartColor,
    gradientChartOptionsConfigurationWithNumbersAndGrid
  } from "./variables/charts.jsx";

  function hexToRGB(hex, alpha) {
    var r = parseInt(hex.slice(1, 3), 16),
      g = parseInt(hex.slice(3, 5), 16),
      b = parseInt(hex.slice(5, 7), 16);
  
    if (alpha) {
      return "rgba(" + r + ", " + g + ", " + b + ", " + alpha + ")";
    } else {
      return "rgb(" + r + ", " + g + ", " + b + ")";
    }
  }
  
  function addDays(date, days) {
    var result = new Date(date);
    result.setDate(result.getDate() + days);
    return result;
  }

  function chart(data,color,lab){
    const label = labels(data)
    const dashboardAllProductsChart = {
        data: canvas => {
            var ctx = canvas.getContext("2d");
            var gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);
            gradientStroke.addColorStop(0, color);
            gradientStroke.addColorStop(1, chartColor);
            var gradientFill = ctx.createLinearGradient(0, 200, 0, 0);
            gradientFill.addColorStop(0, "rgba(128, 182, 244, 0)");
            gradientFill.addColorStop(1, hexToRGB(color, 0.4));
            return {
            labels: label,
            datasets: [
                {
                label: lab,
                borderColor: color,
                pointBorderColor: "#FFF",
                pointBackgroundColor: color,
                pointBorderWidth: 2,
                pointHoverRadius: 4,
                pointHoverBorderWidth: 1,
                pointRadius: 4,
                fill: true,
                backgroundColor: gradientFill,
                borderWidth: 2,
                data: data
                }
            ]
            };
        },
        options: gradientChartOptionsConfigurationWithNumbersAndGrid
        };
        return dashboardAllProductsChart
  }
  

  function labels(arrayin)
  {
      var temp =[]
      for(var i = 0; i<arrayin.length;i++)
      {
          var d = addDays(addDays(new Date(),-arrayin.length+1),i);
          var date = d.getDate();
          var month = d.getMonth() + 1; // Since getMonth() returns month from 0-11 not 1-12
          var year = d.getFullYear();
          temp.push(date + "/" + month + "/" + year) 
      }
      temp.push(null) 
      return temp
  }

const CollElem = (props) => {

var graphdatavar1=props.data1
var graphdatavar2=props.data2
var graphdatavar3=props.data3
var graphdatavar4=props.data4

  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
    <Row md="1">
        <Col xs="12">
            <Button onClick={toggle} style={{borderColor:"white",backgroundColor:"white",color:"black", fontSize: "1.6em", marginBottom: '1rem' }}>
            {props.name}
            </Button>
            <div style={{color: "red", fontSize:"35px", display:props.alert}}> {"Abnormal HR"}</div> 
        </Col>
        <Col xs="12">
            <Row md="2">
                <Col xs="12">
                    <Collapse style={{borderColor:"white",backgroundColor:"white",color:"black", fontSize: "0.7em" }} isOpen={isOpen}>
                        <div className="chart-area">
                            <Line
                            height={window.innerHeight/4}
                            width={window.innerWidth}
                            data={chart(graphdatavar1,"#c9142c","HR").data}
                            options={chart(graphdatavar1,"#c9142c","HR").options}
                            />
                        </div>
                    </Collapse>
                </Col>
                <Col xs="12">
                    <Collapse style={{borderColor:"white",backgroundColor:"white",color:"black", fontSize: "0.7em" }} isOpen={isOpen}>
                        <div className="chart-area">
                            <Line
                            height={window.innerHeight/4}
                            width={window.innerWidth}
                            data={chart(graphdatavar2,"#7214c9","Temp").data}
                            options={chart(graphdatavar2,"#7214c9","Temp").options}
                            />
                        </div>
                    </Collapse>
                </Col>
            </Row>
        </Col>
        <Col xs="12">
            <Row md="2">
                <Col xs="12">
                    <Collapse style={{borderColor:"white",backgroundColor:"white",color:"black", fontSize: "0.7em" }} isOpen={isOpen}>
                        <div className="chart-area">
                            <Line
                            height={window.innerHeight/4}
                            width={window.innerWidth}
                            data={chart(graphdatavar3,"#15bd23","Steps").data}
                            options={chart(graphdatavar3,"#15bd23","Steps").options}
                            />
                        </div>
                    </Collapse>
                </Col>
                <Col xs="12">
                    <Collapse style={{borderColor:"white",backgroundColor:"white",color:"black", fontSize: "0.7em" }} isOpen={isOpen}>
                        <div className="chart-area">
                            <Line
                            height={window.innerHeight/4}
                            width={window.innerWidth}
                            data={chart(graphdatavar4,"#d4ed18","Working Hours").data}
                            options={chart(graphdatavar4,"#d4ed18", "Working Hours").options}
                            />
                        </div>
                    </Collapse>
                </Col>
            </Row>
        </Col>
    </Row>
    <hr />
    </div>
  );
}

export default CollElem;