import React, { Component } from "react";
import { Dropdown, DropdownToggle, DropdownMenu, DropdownItem, Card, Col, Row } from 'reactstrap';
import {isMobile} from "react-device-detect";
import './assets/css/mycss.css';

var employee = ["Alex Sanchex", "Drake Bell","Josh Peck"]

class Dropmenu extends Component {
    constructor(props) {
      super(props);
      this.state = {
        dropdownOpen:false,
        employees:"Select the employee"
      };
      this.toggle = this.toggle.bind(this);
    }

toggle(){
    this.setState({ 
    dropdownOpen:!this.state.dropdownOpen
    })
}

name(pointer){
    this.setState({ 
     employees:employee[pointer],
     dropdownOpen:!this.state.dropdownOpen
    })
    this.props.stateCallback(pointer);
}

render() {
    if(isMobile){
        return (
                <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle style={{fontSize:"30px", alignSelf: "center"}} caret>
                {this.state.employees}
                </DropdownToggle>
                <DropdownMenu style={{fontSize:"30px", alignSelf: "center"}} right>
                <DropdownItem onClick={()=> this.name(0)}>{employee[0]}</DropdownItem>
                <DropdownItem divider/>
                <DropdownItem onClick={()=> this.name(1)}>{employee[1]}</DropdownItem>
                <DropdownItem divider/>
                <DropdownItem onClick={()=> this.name(2)}>{employee[2]}</DropdownItem>
                </DropdownMenu>
                </Dropdown>     
        );
    }
    else{
        return (
            <div id="centerAlign">
            <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
            <DropdownToggle style={{fontSize:"30px"}} caret>
            {this.state.employees}
            </DropdownToggle>
            <DropdownMenu id="centerAlign" style={{fontSize:"30px"}} right>
            <DropdownItem onClick={()=> this.name(0)}>{employee[0]}</DropdownItem>
            <DropdownItem divider/>
            <DropdownItem onClick={()=> this.name(1)}>{employee[1]}</DropdownItem>
            <DropdownItem divider/>
            <DropdownItem onClick={()=> this.name(2)}>{employee[2]}</DropdownItem>
            </DropdownMenu>
            </Dropdown>
            </div>
            
    );
    }
    }
}

export default Dropmenu;