import React, { useState } from 'react';
import Logo from "../assets/logo.svg"
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink } from 'reactstrap';
import {Link} from "react-router-dom";
import {isMobile} from "react-device-detect";

const MyNavBar = (props) => {
  const [collapsed, setCollapsed] = useState(true);

  const toggleNavbar = () => setCollapsed(!collapsed);

  if(isMobile)
  {
    return (
      <div>
        <Navbar light>
        <NavbarBrand style={{fontSize:"30px",color:"white"}} href="/" className="mr-auto"><img src={Logo} style={{width:"14vw",paddingBottom:"10px"}} />
        &nbsp;Capybara
             </NavbarBrand>
          <NavbarToggler style={{backgroundColor:"white",width: "60px",height: "40px",backgroundSize: "cover"}} onClick={toggleNavbar} className="mr-2" />
          <Collapse isOpen={!collapsed} navbar>
            <Nav navbar>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/session/video',
                  state: {token:"return"}
                }}>&nbsp;Login&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/video',
                  state: {token:"enter"}
                }}>&nbsp;Demo&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link target="_blank" id="navig" to={{
                  pathname:'https://github.com/EddOliver/Covital'
                }}>&nbsp;Github&nbsp;</Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
  
  else
  {
    return (
      <div>
        <Navbar light>
        <NavbarBrand style={{fontSize:"42px",color:"white"}} href="/" className="mr-auto"><img src={Logo} style={{height:"80px",paddingTop:"0px"}} />
        &nbsp;Capybara
             </NavbarBrand>
            <Nav style={{color:"white"}}>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/session/video',
                  state: {token:"return"}
                }}>&nbsp;&nbsp;Login&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/video',
                  state: {token:"enter"}
                }}>&nbsp;&nbsp;Demo&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link target="_blank" id="navig" to={{
                  pathname:'https://github.com/EddOliver/Covital'
                }}>&nbsp;&nbsp;Github&nbsp;</Link>
              </NavItem>
            </Nav>
        </Navbar>
      </div>
    );
  }
  }

export default MyNavBar;