/*App.js*/
import React, { Component } from "react";
import Index from "./pages/index"
import Home from "./pages/session/home"
import Dashboard from "./pages/session/dashboard"
import Reco from "./pages/session/rec"
import Video from "./pages/session/video"
import VideoLog from "./pages/session/videolog"
import history from './pages/session/history';
import HomeDoc from "./pages/session-doc/home"
import DashboardDoc from "./pages/session-doc/dashboard"
import RecoDoc from "./pages/session-doc/rec"
import VideoLogDoc from "./pages/session-doc/video"

import {
  Router,
  Route,
  Switch
} from "react-router-dom";

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
      return ( 
      <>
      <Router history={history}>
      <Switch>
        <Route exact path="/" component={Index} />
        <Route exact path="/video" component={VideoLog} />
        <Route exact path="/session/home" component={Home} />
        <Route exact path="/session/dashboard" component={Dashboard} />
        <Route exact path="/session/recommendations" component={Reco} />
        <Route exact path="/session/video" component={Video} />
        <Route exact path="/session-doc/home" component={HomeDoc} />
        <Route exact path="/session-doc/dashboard" component={DashboardDoc} />
        <Route exact path="/session-doc/patients" component={RecoDoc} />
        <Route exact path="/session-doc/video" component={VideoLogDoc} />
        <Route path="*" component={Index} />
      </Switch>
      </Router>
      </>
    );
  }
}

export default App;