import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

const ModalLogin = (props) => {
  const {
    className
  } = props;


  return (
      <Modal isOpen={props.state} toggle={props.toggle} className={className}>
        <ModalHeader toggle={props.toggle}>Wrong Credentials</ModalHeader>
        <ModalBody>
        Wrong username or password, check and try again.
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={props.toggle}>OK</Button>{' '}

        </ModalFooter>
      </Modal>
  );
}

export default ModalLogin;