import React, { Component } from "react";
import './index.css';
import {isMobile} from "react-device-detect";
import Wall from "./assets/iot.jpg"
import Wallm from "./assets/iot.jpg"
import MyNavBar from "./components/navbar-login"
import history from './session/history';
import Pointer from "./assets/pointer.gif"
import ModalLogin from "./LoginModal"

import { Button,CardGroup, Form, FormGroup, Label, Input, Card,CardImg,CardImgOverlay,Row, Col } from 'reactstrap';

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      user: '',
      pass:'',
      token:'return',
      modal:false
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.enterPressed = this.enterPressed.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }

  handleChange(event) {
    if(event.target.name=="email"){
      this.setState({user: event.target.value});
    }
    else if(event.target.name=="password"){
      this.setState({pass: event.target.value});
    }
  }

  loginfun(){
    if(this.state.user.toLowerCase()==="boss" && this.state.pass==""){
      history.push({
        pathname: '/session-doc/home',
        state: { token: "doctor1" }
      })
    } 
    else if(this.state.user.toLowerCase()==="empl1" && this.state.pass==""){
      history.push({
        pathname: '/session/home',
        state: { token: "patient1" }
      })
    } 
    else if(this.state.user.toLowerCase()==="empl2" && this.state.pass==""){
      history.push({
        pathname: '/session/home',
        state: { token: "patient2" }
      })
    } 
    else if(this.state.user.toLowerCase()==="empl3" && this.state.pass==""){
      history.push({
        pathname: '/session/home',
        state: { token: "patient3" }
      })
    } 
    else{
      this.setState({
        modal:true
      })
    }
  }
  

  enterPressed(event) {
    var code = event.keyCode || event.which;
    if(code === 13) { 
      this.loginfun()
    }
  }

  handleSubmit() {
    this.loginfun()
  }

  closeModal(){
    this.setState({
      modal:false,
      user: "",
      pass: ""
    })
  }

  componentWillMount(){
    
  }

  componentWillUnmount(){
    document.removeEventListener("keydown", this.enterPressed, false);
  }

  componentDidMount(){
    document.addEventListener("keydown", this.enterPressed, false);
  }

  render() {

    if(isMobile){
      return (
        <div id="group">
          <ModalLogin toggle={this.closeModal} state={this.state.modal}/>
          <MyNavBar />
      <CardGroup>   
      <Card id="example3" inverse color="dark">
       <CardImg id="example5" src={Wallm} alt="Card image cap" />
        <CardImgOverlay>
        <img src={Pointer} style={{width:"70vw",paddingBottom:"10px"}} />
        <Col> 
        <Form>
      <FormGroup row>
        <Col sm={12}>
          <Input value={this.state.user} onChange={this.handleChange} 
          style={{fontSize:"26px"}} type="email" name="email" id="exampleEmail" placeholder="Email" />
        </Col>
      </FormGroup>
      <FormGroup row>

        <Col sm={12}>
          <Input value={this.state.pass} onChange={this.handleChange} 
          style={{fontSize:"26px"}} type="password" name="password" id="examplePassword" placeholder="Password" />
        </Col>
      </FormGroup>
      <Input type="button" id="mybutton" onClick={this.handleSubmit} style={{fontSize:"30px", border: "2px solid red", borderRadius: "50px"}} size="lg" value="Enter"></Input>
    </Form>
    </Col>
      </CardImgOverlay>
      </Card>
     </CardGroup>
      </div>
        )
    }
    else{
      return (
        <div id="group">
           <ModalLogin toggle={this.closeModal} state={this.state.modal}/>
          <MyNavBar />
      <CardGroup>   
      <Card id="example3" inverse color="dark">
       <CardImg id="example5" src={Wall} alt="Card image cap" />
        <CardImgOverlay>
        <Row>
        <div id="center">
        <img src={Pointer} style={{width:"18vw",paddingBottom:"10px"}} />
        <Col> 
        <Form>
      <FormGroup row>
        <Col sm={12}>
          <Input value={this.state.user} onChange={this.handleChange} 
           style={{fontSize:"26px"}} type="email" name="email" id="exampleEmail" placeholder="Email/User/Phone" />
        </Col>
      </FormGroup>
      <FormGroup row>

        <Col sm={12}>
          <Input value={this.state.pass} onChange={this.handleChange} 
          style={{fontSize:"26px"}} type="password" name="password" id="examplePassword" placeholder="Password" />
        </Col>
      </FormGroup>
      <Input type="button" id="mybutton" onClick={this.handleSubmit} style={{fontSize:"30px", border: "2px solid blue", borderRadius: "50px"}} size="lg" value="Enter"></Input>
    </Form>
    </Col>
    </div>
    </Row>
      </CardImgOverlay>
      </Card>
     </CardGroup>
      </div>
        )
    }
    
    }
  
}

export default Index;