import React, { useState } from 'react';
import Logo from "../assets/logo.svg"
import { Collapse, Navbar, NavbarToggler, NavbarBrand, Nav, NavItem, NavLink} from 'reactstrap';
import {Link} from 'react-router-dom'
import {isMobile} from "react-device-detect";
import Modals from "./modal"

const MyNavBar = (props) => {
  const [collapsed, setCollapsed] = useState(true);

  const toggleNavbar = () => setCollapsed(!collapsed);

  if(isMobile)
  {
    return (
      <div>
        <Modals />
        <Navbar light>
          <NavbarBrand className="mr-auto">
            <img src={Logo} style={{height:"44px",paddingTop:"0px"}} />
              <Link style={{fontSize:"24px",color:"white"}} to={{
              pathname:'/session-doc/home',
              state: {token:props.token}
              }}>&nbsp;Capybara</Link>
          </NavbarBrand>
          <NavbarToggler style={{backgroundColor:"white",width: "60px",height: "40px",backgroundSize: "cover"}} onClick={toggleNavbar} className="mr-2" />
          <Collapse isOpen={!collapsed} navbar>
            <Nav navbar>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/session-doc/home',
                  state: {token:props.token}
                }}>&nbsp;Home&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/session-doc/dashboard',
                  state: {token:props.token}
                }}>&nbsp;Dashboard&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/session-doc/patients',
                  state: {token:props.token}
                }}>&nbsp;Patients&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/session-doc/video',
                  state: {token:props.token}
                }}>&nbsp;Demo&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link target="_blank" id="navig" to={{
                  pathname:'https://github.com/EddOliver/Covital'
                }}>&nbsp;Github&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/',
                  state: {token:"return"}
                }}>&nbsp;Logout&nbsp;</Link>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
  
  else
  {
    return (
      <div>
        <Modals />
        <Navbar light>
        <NavbarBrand className="mr-auto"><img src={Logo} style={{height:"80px",paddingBottom:"0px"}} />
        <Link id="navig" style={{fontSize:"44px"}} to={{
                  pathname:'/session-doc/home',
                  state: {token:props.token}
                }}>&nbsp;Capybara</Link>
             </NavbarBrand>
            <Nav style={{color:"white"}}>
            <NavItem>
              <Link id="navig" to={{
                  pathname:'/session-doc/home',
                  state: {token:props.token}
                }}>&nbsp;&nbsp;Home&nbsp;</Link>
              </NavItem>
              <NavItem>
              <Link id="navig" to={{
                  pathname:'/session-doc/dashboard',
                  state: {token:props.token}
                }}>&nbsp;&nbsp;Dashboard&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/session-doc/patients',
                  state: {token:props.token}
                }}>&nbsp;&nbsp;Employees&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/session-doc/video',
                  state: {token:props.token}
                }}>&nbsp;&nbsp;Demo&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link target="_blank" id="navig" to={{
                  pathname:'https://github.com/EddOliver/Covital'
                }}>&nbsp;&nbsp;Github&nbsp;</Link>
              </NavItem>
              <NavItem>
                <Link id="navig" to={{
                  pathname:'/',
                  state: {token:"return"}
                }}>&nbsp;&nbsp;Logout&nbsp;</Link>
              </NavItem>
            </Nav>
        </Navbar>
      </div>
    );
  }
  }

export default MyNavBar;